*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

:root {
  --primari-white-color: #fff;
  --accent-text-color: #d41443;
  --primary-text-color: #fff;
  --secondary-text-color: #ffa5ba;
  --extra-text-color: #907e82;
  --text-color: #000;
  --bg-hero: #ffb8ca;
  --bg-circle: #ffa5ba;
  --bg-second: #fff5f6;
  --bg-comment: #fff5f6;
  --bg-menu: #e17992;
  --bg-menu-content: #e18298;
  --btn-hover: #ecebeb;
  --btn-hover-accent: #c7133e;
  --timing-function: cubic-bezier(.4, 0, .2, 1);
}

body {
  background-color: var(--primari-white-color);
  color: var(--primari-text-color);
  letter-spacing: .04em;
  font-family: DM Sans, sans-serif;
  font-size: 14px;
  font-weight: 500;
  line-height: 1.86;
}

p, h1, h2, h3, h4, h5, h6 {
  margin: 0;
}

a {
  text-decoration: none;
}

ul, span {
  margin: 0;
  padding: 0;
  list-style: none;
}

img {
  max-width: 100%;
  display: block;
}

.visually-hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  -webkit-clip-path: inset(50%);
  clip-path: inset(50%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.container {
  margin: 0 auto;
  padding-left: 20px;
  padding-right: 20px;
}

@media screen and (min-width: 320px) {
  .container {
    width: 100%;
  }
}

@media screen and (min-width: 480px) {
  .container {
    width: 480px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    width: 768px;
    padding-left: 32px;
    padding-right: 32px;
  }
}

@media screen and (min-width: 1200px) {
  .container {
    width: 1200px;
    padding-left: 16px;
    padding-right: 16px;
  }
}

.section {
  padding-top: 40px;
  padding-bottom: 40px;
}

@media screen and (min-width: 1200px) {
  .section {
    padding-top: 60px;
    padding-bottom: 60px;
  }
}

.btn {
  width: 176px;
  height: 44px;
  background-color: var(--primari-white-color);
  color: var(--accent-text-color);
  border-color: rgba(0, 0, 0, 0);
  border-radius: 22px;
  justify-content: center;
  align-items: center;
  font-family: DM Sans;
  font-size: 16px;
  font-weight: 700;
  line-height: 21px;
  display: flex;
}

.btn:hover, .btn:focus {
  background-color: var(--btn-hover);
}

.btn-icon {
  width: 10px;
  height: 13px;
  margin-left: 13px;
}

.backdrop {
  width: 100%;
  height: 100%;
  z-index: 4;
  opacity: 1;
  transition: opacity 250ms var(--timing-function);
  background-color: rgba(0, 0, 0, .627);
  padding: 30px 15px;
  display: none;
  position: fixed;
  top: 0;
  left: 0;
}

@media screen and (max-width: 324px) {
  .backdrop {
    overflow-y: scroll;
  }
}

.backdrop.is-hidd, .backdrop.is-hidd-duo {
  display: block;
}

.no-scroll-duo {
  overflow: hidden;
}

.backdrop.is-hidd .modal-buy__btn-close-one, .backdrop.is-hidd-duo .modal-buy__btn-close-duo {
  display: none;
}

.modal-buy {
  background: var(--bg-second);
  max-width: 450px;
  z-index: 99;
  border-radius: 14px;
  margin-left: auto;
  margin-right: auto;
  padding: 30px 20px;
  position: relative;
  overflow-y: auto;
  box-shadow: 0 4px 4px rgba(0, 0, 0, .25);
}

@media screen and (min-width: 480px) {
  .modal-buy {
    padding: 30px;
  }
}

.modal-buy__btn-close {
  background: none;
  border-color: rgba(0, 0, 0, 0);
  justify-content: center;
  align-items: center;
  display: flex;
  position: absolute;
  top: 7px;
  right: 5px;
}

.modal-buy__btn-close:hover {
  fill: var(--accent-text-color);
}

.modal-buy__btn-close-icon {
  width: 15px;
  height: 15px;
}

.modal-buy__title {
  letter-spacing: .04em;
  text-transform: uppercase;
  color: var(--accent-text-color);
  text-align: center;
  margin-bottom: 14px;
  font-family: Titan One, sans-serif;
  font-size: 22px;
  font-weight: 400;
  line-height: 1.14;
}

.modal-buy__wrap {
  margin-bottom: 14px;
}

.modal-buy__wrap--center {
  text-align: center;
}

.modal-buy__input {
  -webkit-appearance: none;
  -ms-appearance: none;
  appearance: none;
  width: 12px;
  height: 12px;
  border: .5px solid var(--accent-text-color);
  border-radius: 50%;
  outline: none;
  transition: all .2s linear;
}

@media screen and (max-width: 479px) {
  .modal-buy__input {
    width: 10px;
    height: 10px;
  }
}

input:checked {
  color: var(--accent-text-color);
  border: 6px solid var(--accent-text-color);
}

.modal-buy__label {
  letter-spacing: .04em;
  color: var(--text-color);
  font-family: DM Sans, sans-serif;
  font-size: 14px;
  font-weight: 500;
  line-height: 1.76;
}

.modal-buy__label:not(:last-child) {
  margin-right: 13px;
}

@media screen and (min-width: 320px) and (max-width: 479px) {
  .modal-buy__label {
    font-size: calc(3.75vw - 2px);
  }

  .modal-buy__label:not(:last-child) {
    margin-right: 6px;
  }
}

@media screen and (max-width: 319px) {
  .modal-buy__label {
    font-size: 11px;
  }
}

.modal-buy__label--product {
  font-size: 16px;
}

@media screen and (min-width: 320px) and (max-width: 479px) {
  .modal-buy__label--product {
    font-size: calc(3.125vw + 1px);
  }
}

@media screen and (max-width: 319px) {
  .modal-buy__label--product {
    font-size: 12px;
  }
}

.modal-buy__text {
  letter-spacing: .04em;
  text-transform: uppercase;
  color: var(--secondary-text-color);
  margin-right: 10px;
  font-family: Titan One, sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.14;
  display: inline-block;
}

@media screen and (min-width: 320px) and (max-width: 467px) {
  .modal-buy__text {
    font-size: calc(1.25vw + 8px);
  }
}

@media screen and (max-width: 319px) {
  .modal-buy__text {
    font-size: 12px;
  }
}

.modal-buy__select {
  letter-spacing: .04em;
  color: var(--text-color);
  height: 25px;
  width: 150px;
  font-family: DM Sans, sans-serif;
  font-size: 14px;
  font-weight: 500;
  line-height: 1.76;
}

@media screen and (max-width: 479px) {
  .modal-buy__select {
    height: 20px;
    width: 135px;
    font-size: 13px;
  }
}

.modal-buy__field-input {
  width: 100%;
  height: 30px;
  background-color: var(--primari-white-color);
  border: 2px solid var(--bg-menu);
  transition: background-color 250ms var(--timing-function), border 250ms var(--timing-function);
  border-radius: 6px;
  outline: none;
  margin-bottom: 8px;
  padding-left: 14px;
}

.modal-buy__field-input:focus {
  border: 2px solid var(--accent-text-color);
  background-color: var(--primari-white-color);
  color: var(--accent-text-color);
}

.modal-buy__field-input:focus::-webkit-input-placeholder {
  color: var(--accent-text-color);
}

.modal-buy__comments {
  width: 100%;
  height: 80px;
  background-color: var(--primari-white-color);
  border: 2px solid var(--bg-menu);
  resize: none;
  transition: background-color 250ms var(--timing-function), border 250ms var(--timing-function);
  border-radius: 6px;
  outline: none;
  margin-bottom: 20px;
  padding: 10px 14px;
}

.modal-buy__comments:focus {
  border: 2px solid var(--accent-text-color);
  background-color: var(--primari-white-color);
  color: var(--accent-text-color);
}

.modal-buy__comments:focus::-webkit-input-placeholder {
  color: var(--accent-text-color);
}

::-ms-input-placeholder {
  letter-spacing: .01em;
  color: var(--text-color);
  font-family: DM Sans, sans-serif;
  font-size: 12px;
  line-height: 1.16;
}

::placeholder {
  letter-spacing: .01em;
  color: var(--text-color);
  font-family: DM Sans, sans-serif;
  font-size: 12px;
  line-height: 1.16;
}

.modal-buy__checkbox-wrap {
  margin-bottom: 30px;
}

.modal-buy__checkbox-label {
  letter-spacing: .04em;
  color: var(--text-color);
  justify-content: center;
  align-items: center;
  font-family: DM Sans, sans-serif;
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  line-height: 1.17;
  display: flex;
}

@media screen and (min-width: 320px) and (max-width: 479px) {
  .modal-buy__checkbox-label {
    font-size: calc(1.875vw + 5px);
  }
}

@media screen and (max-width: 319px) {
  .modal-buy__checkbox-label {
    font-size: 12px;
  }
}

.modal-buy__checkbox-custom {
  width: 16px;
  height: 15px;
  border: 2px solid var(--accent-text-color);
  transition: background-color 250ms var(--timing-function);
  border-radius: 3px;
  justify-content: center;
  align-items: center;
  margin-right: 8px;
  display: flex;
}

.modal-buy__checkbox-input:checked + .modal-buy__checkbox-label .modal-buy__checkbox-custom {
  background-color: var(--accent-text-color);
  border: none;
}

.modal-buy__btn {
  background-color: var(--bg-circle);
  color: var(--primary-text-color);
  transition: background-color 250ms var(--timing-function);
  margin-left: auto;
  margin-right: auto;
}

.modal-buy__btn:hover, .modal-buy__btn:focus {
  background-color: var(--btn-hover-accent);
}

.modal-fr {
  max-width: 550px;
  background-color: var(--primari-white-color);
  border-radius: 14px;
  margin-left: auto;
  margin-right: auto;
  padding: 30px 20px;
}

.backdrop.is-hidden .modal-fr {
  display: block;
}

@media screen and (min-width: 480px) {
  .modal-fr {
    padding: 30px;
  }
}

@media screen and (min-width: 768px) {
  .modal-fr {
    min-width: 640px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%)scale(1);
  }

  .backdrop.is-hidden .modal-fr {
    transform: translate(-50%, -50%)scale(1.3);
  }
}

@media screen and (min-width: 1200px) {
  .modal-fr {
    min-width: 750px;
  }
}

.modal-fr__title {
  letter-spacing: .04em;
  text-transform: uppercase;
  color: var(--accent-text-color);
  max-width: 500px;
  text-align: center;
  margin-bottom: 14px;
  margin-left: auto;
  margin-right: auto;
  font-family: Titan One, sans-serif;
  font-size: 22px;
  font-weight: 400;
  line-height: 1.14;
}

.modal-fr__conditions {
  border: 1px solid var(--accent-text-color);
  height: 340px;
  letter-spacing: .04em;
  color: var(--text-color);
  background-color: rgba(204, 147, 162, .09);
  border-radius: 6px;
  margin-top: 0;
  margin-bottom: 20px;
  padding: 20px;
  font-family: DM Sans;
  font-size: 16px;
  font-weight: 500;
  line-height: 1.76;
  overflow-y: auto;
}

.modal-fr__btn-wrap {
  justify-content: space-around;
  align-items: center;
  margin-left: auto;
  margin-right: auto;
  display: flex;
}

@media screen and (max-width: 479px) {
  .modal-fr__btn-wrap {
    flex-direction: column;
  }
}

.modal-fr__btn {
  background-color: var(--accent-text-color);
  color: var(--primary-text-color);
}

.modal-fr__btn:hover, .modal-fr__btn:focus {
  background-color: var(--btn-hover-accent);
}

@media screen and (max-width: 479px) {
  .modal-fr__btn:not(:last-child) {
    margin-bottom: 15px;
  }
}

html {
  scroll-behavior: smooth;
}

.header {
  width: 100%;
  z-index: 3;
  padding-top: 7px;
  position: absolute;
  top: 0;
  left: 0;
}

@media screen and (min-width: 768px) {
  .header {
    padding-top: 32px;
  }
}

@media screen and (min-width: 1200px) {
  .header {
    padding-top: 37px;
  }
}

.header__logo {
  align-items: center;
  display: flex;
}

.no-scroll {
  overflow: hidden;
}

.back-to-top {
  width: 50px;
  height: 50px;
  background-color: var(--secondary-text-color);
  z-index: 10;
  border-color: rgba(0, 0, 0, 0);
  border-radius: 50%;
  display: none;
  position: fixed;
  bottom: 50px;
  right: 20px;
}

.back-to-top:hover, .back-to-top:focus {
  background-color: rgba(199, 19, 61, .573);
}

.back-to-top:after, .back-to-top:before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -40%);
}

.back-to-top:before {
  width: 5px;
  height: 25px;
  background-color: var(--primari-white-color);
  border-top-left-radius: 3px;
  border-top-right-radius: 3px;
}

.back-to-top:after {
  width: 15px;
  height: 15px;
  border-top: 5px solid var(--primari-white-color);
  border-left: 5px solid var(--primari-white-color);
  transform: translate(-50%, -50%)rotate(45deg);
}

.back-to-top-show {
  display: block;
}

.logo-img {
  background-color: var(--accent-text-color);
  border-radius: 50%;
  padding: 2px;
}

.logo-img__text {
  text-transform: uppercase;
